import { createContext, useContext, useEffect, useState } from 'react';
import { getChatPromise } from 'api/getData';
import { useProfileContext } from './profileState';

const initialState = {
  chatData: [],
  setChatData: () => {},
  chatList: [],
  setChatList: () => {},
  onClean: () => {},
  onCleanAll: () => {}
};

export const ChatDataContext = createContext(initialState);

export function ChatDataProvider({ children }) {
  const { chatProfile } = useProfileContext();

  const [chatData, setChatData] = useState([]);
  const [chatList, setChatList] = useState([]);

  useEffect(() => {
    if (!chatProfile?.index) return;
    if (chatData && chatData.length > 0) return;
    // Create a controller
    const controller = new AbortController();
    // Get chat data
    getChatPromise(chatProfile.index).then((res) => {
      if (res && res.length > 0) setChatData(res);
    });
    // Aborts the request when the component umounts
    return () => controller?.abort();
  }, [chatProfile]);

  const onClean = () => {
    setChatList([]);
  };

  const onCleanAll = () => {
    setChatData([]);
    setChatList([]);
  };

  return (
    <ChatDataContext.Provider
      value={{
        chatData,
        setChatData,
        chatList,
        setChatList,
        onClean,
        onCleanAll
      }}
    >
      {children}
    </ChatDataContext.Provider>
  );
}

export function useChatDataContext() {
  return useContext(ChatDataContext);
}
