import { createContext, useContext, useEffect, useState } from 'react';
import { getSelectionPromise } from 'api/getData';
import { useProfileContext } from './profileState';

const initialState = {
  selectionData: [],
  setSelectionData: () => {},
  selections: null,
  setSelections: () => {},
  selectionId: null,
  setSelectionId: () => {},
  selectedPath: null,
  setSelectedPath: () => {},
  onSelect: () => {},
  onClean: () => {},
  onCleanAll: () => {}
};

export const SelectionDataContext = createContext(initialState);

export function SelectionDataProvider({ children }) {
  const { chatProfile } = useProfileContext();

  const [selectionData, setSelectionData] = useState([]);
  const [selectionId, setSelectionId] = useState(null);
  const [selections, setSelections] = useState([]);
  const [selectedPath, setSelectedPath] = useState(null);

  useEffect(() => {
    if (!chatProfile?.index) return;
    if (selectionData && selectionData.length > 0) return;
    // Create a controller
    const controller = new AbortController();
    // Get selection data
    getSelectionPromise(chatProfile.index).then((res) => {
      if (res && res.length > 0) setSelectionData(res);
    });
    // Aborts the request when the component umounts
    return () => controller?.abort();
  }, [chatProfile]);

  useEffect(() => {
    if (selectionId === null) return;
    if (!selectionData || selectionData.length === 0) setSelectionId(null);
    const data = selectionData.filter((s) => {
      return s.selectionId === selectionId;
    });
    if (data && data.length > 0) {
      setSelections(data[0]);
    } else {
      setSelections(null);
    }
  }, [selectionId]);

  const onSelect = (selection) => {
    if (selections.selectionA === selection) {
      setSelectedPath({
        selectedText: selections.selectionA,
        nextChat: selections.ifSelectA
      });
    } else if (selections.selectionB === selection) {
      setSelectedPath({
        selectedText: selections.selectionB,
        nextChat: selections.ifSelectB
      });
    } else if (selections.selectionC === selection) {
      setSelectedPath({
        selectedText: selections.selectionC,
        nextChat: selections.ifSelectC
      });
    }
  };

  const onClean = () => {
    setSelections(null);
    setSelectionId(null);
    setSelectedPath(null);
  };

  const onCleanAll = () => {
    setSelectionData([]);
    onClean();
  };

  return (
    <SelectionDataContext.Provider
      value={{
        selectionData,
        setSelectionData,
        selections,
        setSelections,
        selectionId,
        setSelectionId,
        selectedPath,
        setSelectedPath,
        onSelect,
        onClean,
        onCleanAll
      }}
    >
      {children}
    </SelectionDataContext.Provider>
  );
}

export function useSelectionDataContext() {
  return useContext(SelectionDataContext);
}
