import { createContext, useContext, useState } from 'react';
import { useChatDataContext } from './chatDataState';
import { useSelectionDataContext } from './selectionDataState';

const initialState = {
  restart: false,
  setRestart: () => {},
  onRestart: () => {},
  onNewChat: () => {}
};

export const RestartContext = createContext(initialState);

export function RestartProvider({ children }) {
  const { onClean: onCleanChat } = useChatDataContext();
  const { onCleanAll: onCleanAllChat } = useChatDataContext();
  const { onClean: onCleanSelection } = useSelectionDataContext();
  const { onCleanAll: onCleanAllSelection } = useSelectionDataContext();

  const [restart, setRestart] = useState(false);

  const onRestart = () => {
    onCleanSelection();
    onCleanChat();
    setRestart(false);
  };

  const onNewChat = () => {
    onCleanAllSelection();
    onCleanAllChat();
    setRestart(false);
  };

  return (
    <RestartContext.Provider
      value={{
        restart,
        setRestart,
        onRestart,
        onNewChat
      }}
    >
      {children}
    </RestartContext.Provider>
  );
}

export function useRestartContext() {
  return useContext(RestartContext);
}
