import React, { useEffect, useState } from 'react';
import { useSelectionDataContext } from 'context/selectionDataState';
import { useRestartContext } from 'context/restartState';
import SelectionBox from 'components/SelectionButton/SelectionButton';
import EmptyList from 'components/EmptyList';
import './Selections.css';

function Selections() {
  const { selections } = useSelectionDataContext();
  const { setRestart } = useRestartContext();

  const [SelectionList, setSelectionList] = useState([]);

  useEffect(() => {
    if (selections?.selectionA && selections.selectionB) {
      const selectionsList = [selections.selectionA, selections.selectionB];
      if (selections.selectionC) {
        selectionsList.push(selections.selectionC);
      }
      setSelectionList(selectionsList);
    } else {
      setSelectionList([]);
    }
  }, [selections]);

  useEffect(() => {
    const timerId = setTimeout(() => {
      if (!SelectionList || SelectionList.length === 0) {
        setRestart(true);
      }
    }, 1500);
    // Cleanup the timer when component unmouts
    return () => clearTimeout(timerId);
  });

  return (
    <div className="selection">
      {SelectionList && SelectionList.length > 0 ? (
        SelectionList.map((selection, index) => {
          return (
            <SelectionBox key={`selection-${index}`} selection={selection} />
          );
        })
      ) : (
        <EmptyList />
      )}
    </div>
  );
}

export default Selections;
