import { useEffect, useRef, useState } from 'react';
import { useProfileContext } from 'context/profileState';
import { useChatDataContext } from 'context/chatDataState';
import { useSelectionDataContext } from 'context/selectionDataState';
import Profile from 'components/Profile/Profile';
import EmptyList from 'components/EmptyList';
import './Chats.css';

const ChatBubble = ({ text, isUser }) => {
  return (
    <div className={`bubble ${isUser && 'user'}`}>
      <p>{text}</p>
    </div>
  );
};

const Chat = ({ chat, profileImg }) => {
  const isUser = chat.isUser === '1';
  return (
    <div className={`chat flex-row ${isUser && 'user'}`}>
      {!isUser && <Profile profileImg={profileImg} />}
      <ChatBubble text={chat.text} isUser={isUser} />
    </div>
  );
};

function Chats() {
  const chatRef = useRef(null);

  const { chatProfile } = useProfileContext();
  const { chatData, chatList, setChatList } = useChatDataContext();
  const { setSelectionId, selectedPath, onClean } = useSelectionDataContext();
  const [isFetching, setIsFetching] = useState(true);
  const [Chats, setChats] = useState([]);

  useEffect(() => {
    setIsFetching(true);
    if (!chatProfile?.index) return;
    if (chatData && chatData.length > 0) {
      setIsFetching(false);
    }
  }, [chatProfile, chatData, isFetching]);

  const loadChatsUntilSelection = () => {
    let chats = !chatList || chatList.length === 0 ? [chatData[0]] : chatList;
    if (selectedPath) {
      chats.push({
        chatId: null,
        isUser: '1',
        nextChat: selectedPath.nextChat,
        nextScenario: '',
        text: selectedPath.selectedText
      });
      const nextChat = chatData.filter((s) => {
        return s.chatId === selectedPath.nextChat;
      });
      if (nextChat && nextChat[0]) {
        chats.push(nextChat[0]);
      }
      onClean();
    }
    let isSelection = false;
    while (!isSelection) {
      const latestChat = chats.slice(-1)[0];
      if (latestChat.nextScenario && latestChat.nextScenario !== '') {
        // Now selection
        setSelectionId(latestChat.nextScenario);
        isSelection = true;
      } else if (latestChat.nextChat && latestChat.nextChat !== '') {
        // Keep chat
        const nextChat = chatData.filter((s) => {
          return s.chatId === latestChat.nextChat;
        });
        if (nextChat && nextChat[0]) {
          chats.push(nextChat[0]);
        } else {
          isSelection = true;
        }
      } else {
        // Chat ended
        isSelection = true;
        onClean();
      }
    }
    setChatList(chats);
    setChats(chats);
  };

  useEffect(() => {
    if (isFetching) return;
    if (!chatData || chatData.length === 0) {
      setChatList([]);
      setChats([]);
    } else {
      loadChatsUntilSelection();
    }
  }, [isFetching, chatData, chatList, selectedPath]);

  useEffect(() => {
    // Scroll to bottom
    chatRef.current?.scrollIntoView({ behavior: 'smooth' });
    // chatRef.current?.scrollIntoView({ behavior: 'auto' });
  });

  const chatBubble =
    Chats && Chats.length > 0 ? (
      Chats.map((chat, index) => {
        return (
          <Chat
            key={`chat-${index}`}
            chat={chat}
            profileImg={chatProfile.profileImg}
          />
        );
      })
    ) : (
      <EmptyList />
    );

  return (
    <div className="chats">
      {chatBubble}
      <div ref={chatRef} />
    </div>
  );
}

export default Chats;
