import React, { useEffect } from 'react';
import { useProfileContext } from 'context/profileState';
import './ChoosePerson.css';

const ChatProfile = ({ index, nickname, profileImg, onChoosePerson }) => {
  const onChatProfile = () => {
    onChoosePerson(index);
  };

  return (
    <div className="chat-profile" onClick={onChatProfile}>
      <img src={profileImg} alt="profile image" />
      <p>{nickname}</p>
    </div>
  );
};

function ChoosePerson({ onNextStep }) {
  const { profileImgs, adjectives, animals } = require('assets/profiles.json');
  const { profileList, setProfileList, setChatProfile } = useProfileContext();

  const getTwoRandomIntegers = (max) => {
    const randomNum1 = Math.floor(Math.random() * max);
    let randomNum2 = Math.floor(Math.random() * max);
    while (randomNum1 === randomNum2) {
      randomNum2 = Math.floor(Math.random() * max);
    }
    return [randomNum1, randomNum2];
  };

  useEffect(() => {
    if (profileList && profileList.length > 0) return;
    if (!profileImgs || profileImgs.length === 0) return;
    if (!adjectives || adjectives.length === 0) return;
    if (!animals || animals.length === 0) return;
    // Get profile images
    let nums = getTwoRandomIntegers(profileImgs.length);
    const profiles = [profileImgs[nums[0]], profileImgs[nums[1]]];
    nums = getTwoRandomIntegers(adjectives.length);
    // Get nicknames
    const nicknames = [adjectives[nums[0]], adjectives[nums[1]]];
    nums = getTwoRandomIntegers(animals.length);
    setProfileList([
      {
        profileImg: profiles[0],
        nickname: `${nicknames[0]} ${animals[nums[0]]}`
      },
      {
        profileImg: profiles[1],
        nickname: `${nicknames[1]} ${animals[nums[1]]}`
      }
    ]);
  }, [profileImgs, adjectives, animals]);

  const onChoosePerson = (index) => {
    setChatProfile({ index: index, ...profileList[index - 1] });
    onNextStep();
  };

  return (
    <div className="choose-person select-none">
      <h1>Choose Person</h1>
      {profileList && profileList.length === 2 && (
        <div className="chat-profiles">
          <ChatProfile
            index={1}
            nickname={profileList[0].nickname}
            profileImg={profileList[0].profileImg}
            onChoosePerson={onChoosePerson}
          />
          <ChatProfile
            index={2}
            nickname={profileList[1].nickname}
            profileImg={profileList[1].profileImg}
            onChoosePerson={onChoosePerson}
          />
        </div>
      )}
    </div>
  );
}

export default ChoosePerson;
