import { Route, Routes } from 'react-router-dom';
import { ProfileProvider } from 'context/profileState';
import { ChatDataProvider } from 'context/chatDataState';
import { SelectionDataProvider } from 'context/selectionDataState';
import { RestartProvider } from 'context/restartState';
import NotFound from 'pages/NotFound';
import HomePage from 'pages/HomePage/HomePage';
import './App.css';

function App() {
  return (
    <div className="App-bg">
      <div className="App">
        <Routes>
          <Route
            exact
            path="/"
            element={
              <ProfileProvider>
                <ChatDataProvider>
                  <SelectionDataProvider>
                    <RestartProvider>
                      <HomePage />
                    </RestartProvider>
                  </SelectionDataProvider>
                </ChatDataProvider>
              </ProfileProvider>
            }
          />
          <Route component={NotFound} />
        </Routes>
      </div>
    </div>
  );
}

export default App;
