import React from 'react';
import { useRestartContext } from 'context/restartState';
import './RestartButton.css';

function RestartButton() {
  const { onRestart } = useRestartContext();

  return (
    <div className="restart-btn flex-center" onClick={onRestart}>
      <img src="/restart.png" alt="restart chat" />
    </div>
  );
}

export default RestartButton;
