import { useSelectionDataContext } from 'context/selectionDataState';
import './SelectionButton.css';

function SelectionButton({ selection }) {
  const { onSelect } = useSelectionDataContext();

  if (!selection) {
    return <div className="selection-btn">N/A</div>;
  }

  const onSelectionBtn = () => {
    onSelect(selection);
  };

  return (
    <div className="selection-btn" onClick={onSelectionBtn}>
      {selection}
    </div>
  );
}

export default SelectionButton;
